.grid_control {
  margin-right: 5px;
  padding: 3px;
  font: 11px tahoma,arial,serif;
}

.selected_control {
  background-color: #c6daf4;
  padding: 3px;
}

.table_bord{
  tr:hover {

    td {
      background: #ffe292 none repeat scroll 0 0;
    }
  }
}

.table_bord tr td:first-child {
  width:20px;
}

.table_bord tr td.row_actions  {
  width: 106px;

}

.table_bord tr td.row_actions  a{
  border-radius: 3px;
  position: relative;
  /*padding: 5px 5px;*/
  margin: 0 0 0px 10px;
  min-width: 34px;

  text-align: center;

  border: 1px solid #ddd;
  background-color: #f4f4f4;

  display: inline-block;

}

